.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.githubUser {
  padding: 20px;
}

.label {
  padding: 10px;
}

.repoName {
  display: flex;
  padding: 10px;
  width: 120px;
